<template>
  <v-app>
    <NavbarMenu />
    <div class="content">
      <v-container style="margin-top: 30px" class="mb-12" fluid>
        <v-row dense style="width: 80%; margin: 0 auto">
          <v-col v-for="(pet, index) in animals" :key="index" :cols="flex">
            <v-card style="margin:0 10px">
              <v-img
                :src="pet.imageUrl"
                class="white--text align-end"
                gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                height="200px"
              >
                <v-card-title>
                  名字：{{ pet.animalName }}
                  <!-- 年龄：{{ pet.age }} -->
                </v-card-title>
              </v-img>

              <v-card-actions>
                <v-btn outlined rounded text @click="to(pet)"> 进入病例 </v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </v-app>
</template>

<script>
import axios from 'axios';
import qs from 'qs';
export default {
  name: "CaseReal",
  components: {
    NavbarMenu: () => import("../components/NavbarMenu"),
  },

  data() {
    return {
      flex:'4',
      animals: [
        {
          animalId: "001",
          animalName: "pet1",
          // age: "3",
          caseId:'011',
          imageUrl:
            "https://img1.baidu.com/it/u=4011382425,2248971044&fm=26&fmt=auto&gp=0.jpg",
        },
        {
          animalId: "002",
          animalName: "pet2",
          // age: "4",
          caseId:'012',
          imageUrl: require("../assets/狗头.jpg"),
        },
        {
          animalId: "003",
          animalName: "pet3",
          // age: "3",
          caseId:'013',
          imageUrl: require("../assets/狗头.jpg"),
        },
        {
          animalId: "004",
          animalName: "pet4",
          // age: "3",
          caseId:'014',
          imageUrl: require("../assets/狗头.jpg"),
        },
      ],
    };
  },
  created(){
    // console.log('大类',this.$route.query.caseclass);
    console.log('具体病种',this.$route.query.caseName);
    // axios.get
    this.updata()
  },
  methods: {
    updata(){
      axios.post('/case/animal',qs.stringify({
        // categoryId:this.$route.query.caseclass,
        diseaseName:this.$route.query.caseName,
      })).then((res)=>{
        console.log('res:',res)
        if (res.data.code === 200){
          this.animals = res.data.data
        }
        else if (res.data.code === -1) {
          window.sessionStorage.clear(); // 清空token
          var href = res.data.data.domain;
          window.location.href = href;
        }
        else {
          this.$store.dispatch('snackbar/openSnackbar',{
            msg:res.data.msg,
            color:'primary'
          })
        }
      }).catch((err)=>{
        console.log(err);
      })
    },
    to(pet) {
      // console.log(pet.animalId)
      this.$router.push({
        path:'/case/real/item',
        query:{
          caseId:pet.caseId
        }
      })
    },
  },
};
</script>


<style scoped>
.content {
  margin-top: 70px;
  height: 100%;
  width: 100%;
  background: linear-gradient(
    144deg,
    rgb(253, 140, 118) 0%,
    rgb(81, 120, 187) 100%
  );
}
</style>